import React, { useEffect, useState } from 'react';
import { DatePicker as DatePickerMobile, List } from 'antd-mobile';
import moment from 'moment';

import calendar from '../../assets/icons/calendar.svg';
import closeDate from '../../assets/icons/closeDate.svg';
import styles from './index.less';

interface DatePickerProps {
  mode?: 'datetime' | 'date' | 'year' | 'month' | 'time';
  suffixIcon?: string;
  showSuffixIcon?: boolean;
  placeholder?: string;
  value?: string;
  onChange?: (value: string) => void;
}

const modeType = {
  date: 'YYYY-MM-DD',
  time: 'HH:mm'
}

const DatePicker: React.FC<DatePickerProps> = (props) => {
  const [effectiveDate, setEffectiveDate] = useState<any>();
  const { mode = "date", suffixIcon, showSuffixIcon, placeholder = '', value='', onChange } = props;

  const handleChnage = (date) => {
    if (onChange) {
      onChange(moment(date).format(modeType[mode]))
    }
    setEffectiveDate(date)
  }

  const handleClear = () => {
    if (onChange) {
      onChange('');
    }

    setEffectiveDate(null);
  }

  useEffect(() => {
    if (value) {
      setEffectiveDate(moment(value, modeType[mode]))
    }
  }, [value])
  
  return (
    <div className={styles.dateContainer}>
      <DatePickerMobile 
        mode={mode}
        value={effectiveDate}
        onChange={handleChnage}
        extra={placeholder}
      >
        <List.Item arrow="horizontal">Date</List.Item>
      </DatePickerMobile>
      {effectiveDate && <img src={closeDate} alt="清空选择" onClick={handleClear}/>}
      {showSuffixIcon && !effectiveDate && <img src={suffixIcon || calendar} alt="日期"/>}
    </div>
  )
}

export default DatePicker;