import React, { ReactNode } from 'react';

import styles from './index.less';

interface MaskTopProps {
  children: ReactNode;
  visible: boolean;
  onCancel: Function;
}

const MaskTop: React.SFC<MaskTopProps> = (props) => {
  const { children, visible } = props;

  return (
    <div className={styles.maskTop} style={{top: visible ? 0 : '-1000px'}}>
      {children}
    </div>
  )
}

export default MaskTop;