import React, { ReactNode } from 'react';

import MaskTop from './MaskTop';
import MaskMiddle from './MaskMiddle';
import MaskBottom from './MaskBottom';
import styles from './index.less';

interface MaskProps {
  children: ReactNode;
  visible: boolean;
  onCancel: Function;
  position?: 'top' | 'middle' | 'bottom';
  title?: string;
  footer?: ReactNode;
}

const Mask: React.SFC<MaskProps> = (props) => {
  const { children, visible, onCancel, position = "bottom", title = "", footer } = props;
  
  return (
    <div className={styles.maskContainer} style={{zIndex: visible ? 100 : -100}}>
      {visible && <div className={styles.maskBg} onClick={()=>onCancel()}/> }
      {
        position === 'bottom' && <MaskBottom visible={visible} onCancel={onCancel} title={title}>
          {children}
        </MaskBottom>
      }
      {
        position === 'top' && <MaskTop visible={visible} onCancel={onCancel}>
          {children}
        </MaskTop>
      }
      {
        position === 'middle' && <MaskMiddle visible={visible} onCancel={onCancel} title={title} footer={footer}>
          {children}
        </MaskMiddle>
      }
    </div>
  )
}

export default Mask;