import React, { useState } from 'react';
import { Picker as PickerMobile, List } from 'antd-mobile';

import styles from './index.less';

interface PickerDateProps {
  label: string | number;
  value: string | number;
}

interface PickerProps {
  data: PickerDateProps[];
  onChange?: (date?: string | number) => void;
}

const Picker:React.SFC<PickerProps> = (props) => {
  const { data, onChange } = props;
  const [value, setValue] = useState<string[] | number[]>([]);

  const handleChange = (val) => {
    setValue(val);
    if (onChange) {
      onChange(val);
    }
  }

  return (
    <div className={styles.pickerContainer}>
      <PickerMobile data={data} cols={1} value={value} onChange={handleChange}>
        <List.Item arrow="horizontal">Single</List.Item>
      </PickerMobile>
    </div>
  )
}

export default Picker;