import React, { ReactNode } from 'react';

import styles from './index.less';

interface ActiveInfoProps {
  title: string | number;
  value: string | number | ReactNode;
  alignItems?: 'flex-start' | 'center' | 'flex-end';
  required?: boolean;
  labelCol?: { span: number };
}

const ActiveInfo: React.SFC<ActiveInfoProps> = (props) => {
  const { title, value, alignItems = 'center', required, labelCol = {span: 0} } = props;

  return (
    <div className={styles.activeInfo} style={{alignItems: alignItems}}>
      <span 
        className={styles.title} 
        style={{
          flex: labelCol.span ? labelCol.span / 10 : undefined, 
          justifyContent: labelCol.span ? 'flex-end' : 'flex-start' 
        }}>
          {required && <span style={{color: 'red', margin: '5px 3px 0 0'}}>*</span>}
          {title}:
      </span>
      <div className={styles.content} style={{flex: labelCol.span ? (1 - labelCol.span / 10) : 1 }}>{value || '--'}</div>
    </div>
  )
}

export default ActiveInfo;