import React from 'react';

import styles from './index.less';

interface TextareaProps {
  showCount?: boolean;
  placeholder?: string;
  onChange?: any;
}

const Textarea: React.SFC<TextareaProps> = (props) => {
  const { placeholder = "请输入", showCount, onChange } = props;

  const hanleChange = (e) => {
    if (onChange) {
      onChange(e.target.value)
    }
  }

  return (
    <div className={styles.textareaBox}>
      <textarea className={styles.textarea} placeholder={placeholder} onChange={hanleChange} />
      {showCount && <div className={styles.nums}>0/100</div>}
    </div>
  )
}

export default Textarea;