import React, { ReactNode } from 'react';

import close from '../../assets/icons/close.svg';
import styles from './index.less';

interface MaskBottomProps {
  children: ReactNode;
  visible: boolean;
  onCancel: Function;
  title?: string;
}

const MaskBottom: React.SFC<MaskBottomProps> = (props) => {
  const { children, visible, onCancel, title = "" } = props;

  return (
    <div className={styles.maskBottom} style={{bottom: visible ? 0 : '-1000px'}}>
      <div className={styles.header}>
        {title}
        <img src={close} alt="关闭" className={styles.close} onClick={()=>onCancel()}/>
      </div>
      <div className={styles.bodyer}>
        {children}
      </div>
    </div>
  )
}

export default MaskBottom;