import React, { ReactNode } from 'react';

import close from '../../assets/icons/close.svg';
import styles from './index.less';

interface MaskMiddleProps {
  children: ReactNode;
  visible: boolean;
  onCancel: Function;
  title?: string;
  footer?: ReactNode;
}

const MaskMiddle: React.FC<MaskMiddleProps> = (props) => {
  const { children, visible, onCancel, title = "", footer } = props;

  return (
    <div className={styles.maskMiddle} style={{display: visible ? 'block' : 'none'}}>
      <div className={styles.header}>
        {title}
        <img src={close} alt="关闭" className={styles.close} onClick={()=>onCancel()}/>
      </div>
      <div className={styles.bodyer}>
        {children}
      </div>
      <div>
        {footer}
      </div>
    </div>
  )
}

export default MaskMiddle;