import React, { useEffect, useState } from 'react';
import { Toast } from 'antd-mobile';
import { connect } from 'dva';
import moment from 'moment';

import { DecryptDes, GetCookie, getQueryValue } from '../../utils/func';
import { UserState } from '../Index/model';
import { fetchFollowCustomer, fetchAddSchedule, fetchCheckAdd } from '../../services/detail';
import { CommunicationStateType } from '../datas';
import right_arrow from '../../assets/icons/right_arrow.svg';
import ActiveInfo from '../../components/ActiveInfo';
import Picker from '../../components/Picker';
import DatePicker from '../../components/DatePicker';
import Textarea from '../../components/Textarea';
import Button from '../../components/Button';
import Mask from '../../components/Mask';
import Input from '../../components/Input';
import { DurationOptions, RemarkOptions } from './options';
import styles from './index.less';

interface FollowupProps {
  history: any;
  user: UserState;
}

const FollowUp: React.FC<FollowupProps> = (props) => {
  const { history, user } = props;
  const type = getQueryValue('type', history.location.search);

  const [visible, setVisible] = useState<boolean>(false);
  const [comOptions, setComOptions] = useState<any[]>([]);
  const [communicationState, setCommunicationState] = useState<string>('');
  const [fyp, setFyp] = useState<string>('');
  const [porscheObjection, setPorscheObjection] = useState<string>('');
  const [startDate, setStartDate] = useState<string>('');
  const [startTime, setStartTime] = useState<string>('');
  const [endTime, setEndTime] = useState<string>('');
  const [duration, setDuration] = useState<string>('');
  const [remark, setRemark] = useState<string>('');
  const [nextCommucationDate, setNextCommucationDate] = useState<string>('');
  const [summary, setSummary] = useState<string>('');

  const handleSubmit = () => {
    if (type !== 'wealth' && !communicationState) {
      Toast.fail('沟通类型不能为空')
      return;
    } else {
      fetchFollowCustomer({
        communicationState: parseInt(communicationState), // 沟通类型
        estimateFyp: fyp, // 预估fyp
        porscheObjection, // 主要异议
        tid: parseInt(DecryptDes(GetCookie('tid'))), // 训练营客户唯一id
        nextTime: startDate, // 下次沟通日期
        nextClock: startTime, // 下次沟通时间
        nextEndClock: endTime, // 下次沟通结束时间
        unionId: DecryptDes(GetCookie('unionid')), // 用户unionId
        external_user_id: DecryptDes(GetCookie('externaluserid')), // 客户企微Id
        user_id: sessionStorage.getItem('userId') // 理财师id
      }).then((res: any) => {
        if (res.code === 200) {
          Toast.success('保存成功');
          history.go(-1);
        }
      })
    }
  }

  const handleSave = async () => {
    console.log(startDate)
    console.log(startTime)
    console.log(moment(`${startDate} ${startTime}`).unix())
    const res: any = await fetchCheckAdd({
      user_id: sessionStorage.getItem('userId'),
      start_time: moment(`${startDate} ${startTime}`).unix()
    })
    console.log(res)
    if (res.code === 200 && res.count === 0) {
      let conversion = parseInt(duration[0]) / 60;
      let hour = parseInt(startTime.split(':')[0]);
      let minute = parseInt(startTime.split(':')[1]);

      hour = hour + parseInt(`${conversion}`.split('.')[0]);
      minute = minute + (parseInt(`${conversion}`.split('.')[1], 10) * 6 || 0)

      if (minute > 60) {
        hour += 1;
        minute = minute - 60;
        if (hour < 24) {
          setNextCommucationDate(`${startDate} ${startTime}-${hour}:${minute > 10 ? minute : `0${minute}`}`)
          setEndTime(`${hour}:${minute > 10 ? minute : `0${minute}`}`)
        } else {
          setNextCommucationDate(`${startDate} ${startTime}-次日${hour - 24}:${minute > 10 ? minute : `0${minute}`}`)
          setEndTime(`${hour - 24}:${minute > 10 ? minute : `0${minute}`}`)
        }
      } else {
        setNextCommucationDate(`${startDate} ${startTime}-${hour}:${minute > 10 ? minute : `0${minute}`}`)
        setEndTime(`${hour}:${minute > 10 ? minute : `0${minute}`}`)
      }

      console.log(conversion)

      const resp: any = await fetchAddSchedule({
        user_id: sessionStorage.getItem('userId'),
        start_time: moment(`${startDate} ${startTime}`).unix(),
        summary,
        remind_time: remark[0] ? parseInt(remark[0], 10) * 60 : 0,
        end_time: moment(`${startDate} ${startTime}`).unix() + conversion * 60 * 1000,
      })

      if (resp.code === 200) {
        setVisible(false)
      } else {
        Toast.fail('新增日程失败');
      }
    } else {
      Toast.fail('当前时间段已有行程，请重新选择');
    }

    setVisible(false)
  }

  useEffect(() => {
    const options = [];
    Object.keys(CommunicationStateType).forEach((i: string) => {
      options.push({
        label: CommunicationStateType[i],
        value: i
      })
    })

    setComOptions(options);
  }, [])

  return (
    <div style={{ width: '100%', height: '100%' }}>
      <Mask position="middle" visible={visible} onCancel={() => setVisible(false)} title="设置下次跟进时间">
        <div style={{ padding: '0 0 10px 10px' }}>
          <div className={styles.listItem}>
            <Input placeholder="请输入跟进主题" value={summary} onChange={val => setSummary(val)} />
          </div>
          <div className={styles.listItem}>
            <ActiveInfo title="开始" value={
              <div className={styles.dataItem}>
                <DatePicker placeholder="选择日期" onChange={val => setStartDate(val)} />
                <span style={{ margin: '0 8px 0 3px' }}>|</span>
                <DatePicker mode="time" placeholder="选择时间" onChange={val => setStartTime(val)} />
              </div>
            } />
          </div>
          <div className={styles.listItem}>
            <ActiveInfo title="时长" value={
              <div className={styles.dataItem}>
                <Picker data={DurationOptions} onChange={(val: string) => setDuration(val)} />
              </div>
            } />
          </div>
          <div className={styles.listItem}>
            <ActiveInfo title="提醒" value={
              <div className={styles.dataItem}>
                <Picker data={RemarkOptions} onChange={(val: string) => setRemark(val)} />
              </div>
            } />
          </div>
          <div style={{ display: 'flex', justifyContent: 'flex-end', paddingRight: '10px' }}>
            <Button size="small" style={{ marginRight: '10px' }} onClick={() => setVisible(false)}>取消</Button>
            <Button type="primary" size="small" onClick={handleSave}>保存</Button>
          </div>
        </div>
      </Mask>
      <div style={{ background: '#fff' }}>
        {
          type === 'wealth' &&
          <div style={{ padding: '10px 20px 10px 0', borderBottom: '1px solid #F3F3F3' }}>
            <div style={{ marginTop: '15px' }}>
              <ActiveInfo
                labelCol={{ span: 2.5 }}
                title="跟进内容"
                alignItems="flex-start"
                value={
                  <div style={{ height: '6rem' }}>
                    <Textarea onChange={e => setPorscheObjection(e)} />
                  </div>
                } />
            </div>
          </div>
        }
        {
          type !== 'wealth' &&
          <div style={{ padding: '10px 20px 10px 0', borderBottom: '1px solid #F3F3F3' }}>
            <ActiveInfo
              required
              title="沟通类型"
              labelCol={{ span: 2.5 }}
              value={
                <div className={styles.dateContainer}>
                  <Picker data={comOptions} onChange={val => setCommunicationState(val[0])} />
                </div>
              } />
            <ActiveInfo
              title="预估FYP"
              labelCol={{ span: 2.5 }}
              value={
                <div style={{display: 'flex', alignItems: 'center', border: '1px solid #f0f0f0' }}>
                  <div style={{flex: '1', padding: '0 10px'}}>
                    <Input onChange={val => setFyp(val)} />
                  </div>
                  <div style={{padding: '5px 12px', borderLeft: '1px solid #f0f0f0'}}>
                    元
                  </div>
                </div>
              } />
            <div style={{ marginTop: '15px' }}>
              <ActiveInfo
                labelCol={{ span: 2.5 }}
                title="主要异议"
                alignItems="flex-start"
                value={
                  <div style={{ height: '6rem' }}>
                    <Textarea onChange={e => setPorscheObjection(e)} />
                  </div>
                } />
            </div>
          </div>
        }
        <div style={{ padding: '5px 20px' }}>
          <ActiveInfo title="下次沟通时间" value={
            <div style={{ display: 'flex', alignItems: 'center', color: '#9E9E9E', justifyContent: 'flex-end' }} onClick={() => setVisible(true)}>
              {nextCommucationDate || <>
                设置 <img src={right_arrow} alt="右箭头" />
              </>}
            </div>
          } />
        </div>
      </div>
      <div className={styles.btnContainer}>
        <Button style={{ flex: 4, margin: '10px 5px 10px 10px' }} cancel onClick={() => {
          history.go(-1);
        }}>取消</Button>
        <Button type="primary" style={{ flex: 8, margin: '10px 5px' }} onClick={handleSubmit}>保存</Button>
      </div>
    </div>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(FollowUp);